.school_card {
  height: 12.5rem;
  width: 20.5rem;
  background-size: cover;
  backdrop-filter: blur(10px);
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 5px;
  font-family: "Inter", sans-serif;
}
.school_card > .school_infos {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  color: white;
  height: 100%;
  width: 100%;
  background: rgba(0, 0, 0, 0.5);
  line-height: 0;
}

.school_infos > p {
  font-size: small;
  font-weight: 200;
}

.school_infos > h4 {
  font-size: xx-large;
}